import {HashRouter,Link,Switch,Route} from 'react-router-dom'
import '../css/ly2_2.css'
import Ly2_2_1 from '../nav/ly2_2_1'
import Ly2_2_2 from '../nav/ly2_2_2'
import Ly2_2_3 from '../nav/ly2_2_3'
import Ly2_2_4 from '../nav/ly2_2_4'
import Ly2_2_5 from '../nav/ly2_2_5'
import Ly2_2_6 from '../nav/ly2_2_6'
import Ly2_2_7 from '../nav/ly2_2_7'


function Ly2_2(){
    return(

        <div className="Todo">
            <div className="Todolist">待办事项</div>
            <HashRouter>
                <div className="Todo__list">
                    <ul>
                        <Link className="left_l" to="/app/ly2/ly2_2/" >今日需联系客户</Link>
                        <Link className="left_l" to="/app/ly2/ly2_2/ly2_2_2">分配给我的线索</Link>
                        <Link className="left_l" to="/app/ly2/ly2_2/ly2_2_3">分配给我的客户</Link>
                        <Link className="left_l" to="/app/ly2/ly2_2/ly2_2_4">待审核合同</Link>
                        <Link className="left_l" to="/app/ly2/ly2_2/ly2_2_5">待审核回款</Link>
                        <Link className="left_l" to="/app/ly2/ly2_2/ly2_2_6">待回款提醒</Link>
                        <Link className="left_l" to="/app/ly2/ly2_2/ly2_2_7">即将到期的合同</Link>
                    </ul>
                </div>
            <Switch>
                <Route path="/app/ly2/ly2_2" component={Ly2_2_1} exact></Route>
                <Route path="/app/ly2/ly2_2/ly2_2_2" component={Ly2_2_2}></Route>
                <Route path="/app/ly2/ly2_2/ly2_2_3" component={Ly2_2_3}></Route>
                <Route path="/app/ly2/ly2_2/ly2_2_4" component={Ly2_2_4}></Route>
                <Route path="/app/ly2/ly2_2/ly2_2_5" component={Ly2_2_5}></Route>
                <Route path="/app/ly2/ly2_2/ly2_2_6" component={Ly2_2_6}></Route>
                <Route path="/app/ly2/ly2_2/ly2_2_7" component={Ly2_2_7}></Route>
            </Switch>
            </HashRouter>
        </div>

    )
}
export default Ly2_2