import {HashRouter,Switch,Route,Link} from 'react-router-dom'
import Ly1 from '../view/ly1'
import Ly2 from '../view/ly2'
import Ly3 from '../view/ly3'
import Ly4 from '../view/ly4'
import { Popover} from 'antd';
import 'antd/dist/antd.css'


function Ly() {
    const content = (
        <div>
          <h4>您暂未开通特权</h4>
            <div className='he-poa-p'>
                <p>为了给您提供更好的服务支持</p>
                <p>建议您购买官方授权</p>
            </div>
            <p className='p3'>400-0812-558</p>
        </div>
      );

      const content2 = (
        <div className='content2'>
          <p><span className='iconfont'>&#xe695;</span>个人中心</p>
          <p><span className='iconfont'>&#xe601;</span>退出登入</p>
          <p><span className='iconfont'>&#xe66c;</span>版本 V9.3.2.191220</p>
          <p className='p2'><button>进入企业管理后台</button></p>
        </div>
      );
  return(
    <div>
        <div className='head clearfix'>
            <div className='logo'>
                <img src="http://crm.cimns.com/static/img/logo.3e34073.png"  alt="" />
            </div>
            <div className='ly'>
                <HashRouter>
                <ul className='ly-ul'>
                <li><Link to="/app/"><span className='iconfont'>&#xe625;</span><em>办公</em></Link></li>
                <li><Link to="/app/ly2"><span className='iconfont'>&#xe68e;</span><em>客户管理</em></Link></li>
                <li><Link to="/app/ly3"><span className='iconfont'>&#xe673;</span><em>商业智能</em></Link></li>
                <li><Link to="/app/ly4"><span className='iconfont'>&#xe628;</span><em>项目管理</em></Link></li>
                </ul>
                </HashRouter>
            </div>
            <div className='he-fr clearfix'>
                <div className='he-vip'>
                <Popover content={content}>
                    <p className='he-vip-p' type="primary">开通特权</p>
                </Popover>
                </div>
                <div className='he-fr-fr clearfix'>
                    <div className='he-fr-img'>
                        <img src="http://crm.cimns.com/public/uploads/20210408/thumb_f92fa56a3a27d7eb77ae1191bff75c4f.jpg" alt="" />
                    </div>
                    <div className='he-fr-fr-fr'>
                    <Popover placement="bottom" content={content2} trigger="click">
                        <button className='iconfont'>&#xe607;</button>
                    </Popover>
                    </div>
                </div>
            </div>
        </div>
        <div className='content'>
            <HashRouter>
                <Switch>
                    <Route path="/app/ly2" component={Ly2}></Route>
                    <Route path="/app/ly3" component={Ly3}></Route>	
                    <Route path="/app/ly4" component={Ly4}></Route>
                    <Route path="/app/" component={Ly1}></Route>
                </Switch>
            </HashRouter>
        </div>
    </div>
  )
};

export default Ly