import { Layout, Menu} from 'antd';
import { Popover} from 'antd';
import React,{Component}from 'react'
import {Switch,Route,Link} from 'react-router-dom'
import Ly1_1 from './ly1_1'
import Ly1_2 from './ly1_2'
import Ly1_3 from './ly1_3'
import Ly1_4 from './ly1_4'
import Ly1_5 from './ly1_5'
import Ly1_6 from './ly1_6'
import Ly1_7 from './ly1_7'


import 'antd/dist/antd.css'
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
  } from '@ant-design/icons';
  
  const { Sider, Content } = Layout;
  const content = (
    <div className='tlist'>
      <ul className='clearfix'>
        <li><p><span className='iconfont'>&#xe63c;</span>日志</p></li>
        <li><p><span className='iconfont'>&#xe826;</span>任务</p></li>
        <li><p><span className='iconfont'>&#xe749;</span>公告</p></li>
        <li><p><span className='iconfont'>&#xe695;</span>审批</p></li>
        <li><p><span className='iconfont'>&#xe6f5;</span>日程</p></li>
        
      </ul>
    </div>
);
  let type = true


  export default class Ly1 extends Component {
    
    state = {
      collapsed: false,
    }
    toggle = () => {
      this.setState({
        collapsed: !this.state.collapsed,
      });
    };
  
    render() {
      return (
        <Layout style={{minHeight:'100%'}} className='ppoe'>
          <Sider trigger={null} collapsible collapsed={this.state.collapsed}>
            <Popover placement="right" content={content} trigger="hover">
              <div className="logo2">
                <div className='ks'>
                    <span style={{display:type === true?'block':'none'}}>快速创建</span><i>&gt;</i>
                </div>
              </div>
            </Popover>
            <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']}>
              <Menu.Item key="1" icon={<i className='iconfont'>&#xe625;</i>}>
                <Link to="/app/">工作台</Link>
              </Menu.Item>
              <Menu.Item key="2" icon={<i className='iconfont'>&#xe6f5;</i>}>
                <Link to="/app/ly1_2">日程</Link>
              </Menu.Item>
              <Menu.Item key="3" icon={<i className='iconfont'>&#xe826;</i>}>
                <Link to="/app/ly1_3">任务</Link>
              </Menu.Item>
              <Menu.Item key="4" icon={<i className='iconfont'>&#xe749;</i>}>
                <Link to="/app/ly1_4">公告</Link>
              </Menu.Item>
              <Menu.Item key="5" icon={<i className='iconfont'>&#xe63c;</i>}>
                <Link to="/app/ly1_5">日志</Link>
              </Menu.Item>
              <Menu.Item key="6" icon={<i className='iconfont'>&#xe695;</i>}>
                <Link to="/app/ly1_6">审批</Link>
              </Menu.Item>
              <Menu.Item key="7" icon={<i className='iconfont'>&#xe6af;</i>}>
                <Link to="/app/ly1_7">通讯录</Link>
              </Menu.Item>
              
            </Menu>
            <div className='btn1' onClick={()=>{type = !type}}>
            {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
                className: 'trigger',
                onClick: this.toggle,
              })}
            </div>
          </Sider>
          <Layout className="site-layout">
            <Content className="site-layout-background" style={{margin: '24px 16px',padding: 24,minHeight: 280,}} >
                <Switch>
                    <Route path="/app/ly1_2" component={Ly1_2}></Route>
                    <Route path="/app/ly1_3" component={Ly1_3}></Route>	
                    <Route path="/app/ly1_4" component={Ly1_4}></Route>
                    <Route path="/app/ly1_5" component={Ly1_5}></Route>
                    <Route path="/app/ly1_6" component={Ly1_6}></Route>
                    <Route path="/app/ly1_7" component={Ly1_7}></Route>
                    
                    <Route path="/app/" component={Ly1_1}></Route>
                </Switch>
            </Content>
          </Layout>
        </Layout>
      );
    }
  }