//   config.headers={
//       authKey:login.authKey,
//       sessionId:login.sessionId
//   }
//   return config
// })

axios.interceptors.request.use((config)=>{
  if(localStorage.login){
    var login = JSON.parse(localStorage.login)
    config.headers={
        authKey:login.authKey,
        sessionId:login.sessionId
    }
    return config
  }
  else{
    return config
  }
  
})


function App() {
  return (
    <div className="App">
      <Ly></Ly>
    </div>
  );
}

export default App;
