import React, {
    Component
} from 'react'
import {
    TreeSelect
} from 'antd';

class ly6 extends Component {
    state = {
        value: undefined,
        treeData: [{
                id: 1,
                pId: 0,
                value: '1',
                title: '待审核',
                isLeaf: true
            },
            {
                id: 2,
                pId: 0,
                value: '2',
                title: '已审核',
                isLeaf: true
            }
        ],
    };

    genTreeNode = (parentId, isLeaf = false) => {
        const random = Math.random().toString(36).substring(2, 6);
        return {
            id: random,
            pId: parentId,
            value: random,
            title: isLeaf ? 'Tree Node' : 'Expand to load',
            isLeaf,
        };
    };

    onLoadData = treeNode =>
        new Promise(resolve => {
            const {
                id
            } = treeNode.props;
            setTimeout(() => {
                this.setState({
                    treeData: this.state.treeData.concat([
                        this.genTreeNode(id, false),
                        this.genTreeNode(id, true),
                    ]),
                });
                resolve();
            }, 300);
        });

    onChange = value => {
        console.log(value);
        this.setState({
            value
        });
    };

    render() {
        const {
            treeData
        } = this.state;
        return ( <
            TreeSelect treeDataSimpleMode style = {
                {
                    width: '180px'
                }
            }
            value = {
                this.state.value
            }
            dropdownStyle = {
                {
                    maxHeight: 300,
                    overflow: 'auto'
                }
            }
            placeholder = "待审核"
            loadData = {
                this.onLoadData
            }
            onChange = {
                this.onChange
            }
            treeData = {
                treeData
            }
            />
        );
    }
}

export default ly6