import '../css/ly3.css'
import { Layout, Menu, } from 'antd';
import React,{Component} from 'react'
import {
    UserOutlined,
    TeamOutlined,
    FilterOutlined,
    LineChartOutlined,
    AppstoreOutlined,
    BarChartOutlined,
    FundOutlined,
    AimOutlined,
} from '@ant-design/icons';
import { Link, Switch ,Route, HashRouter} from 'react-router-dom';
// 跳转
import kehu2 from '../shop/kehu2';
import kehu3 from '../shop/kehu3'
import kehu4 from '../shop/kehu4'
import kehu5 from '../shop/kehu5'
import kehu6 from '../shop/kehu6'
import kehu7 from '../shop/kehu7'
import loudou2 from '../shop/loudou2'
import loudou3 from '../shop/loudou3'
import loudou4 from '../shop/loudou4'
import yeji2 from '../shop/yeji2'
import yeji3 from '../shop/yeji3'
import yeji4 from '../shop/yeji4'
import yeji5 from '../shop/yeji5'
import hua2 from '../shop/hua2'
import hua3 from '../shop/hua3'
import hua4 from '../shop/hua4'
import hua5 from '../shop/hua5'
import product2 from '../shop/product2'
import product3 from '../shop/product3'
import pai2 from '../shop/pai2'
import pai3 from '../shop/pai3'
import pai4 from '../shop/pai4'
import pai5 from '../shop/pai5'
import pai6 from '../shop/pai6'
import pai7 from '../shop/pai7'
import pai8 from '../shop/pai8'
import pai9 from '../shop/pai9'
import pai10 from '../shop/pai10'
import ban1 from '../shop/ban1'
import ban2 from '../shop/ban2'
import mubiao from '../shop/mubiao'
// import axios from 'axios'
 // z组件序所需
