const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

export default class ly3 extends Component{
  
  state = {
    collapsed: false,
  };

  
  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  render() {
    const { collapsed } = this.state;
    return (
      <Layout style={{ minHeight: '85vh' }}>
      <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse} className='menu_list'>
        <div className="logo" />
        <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
          <SubMenu key="sub1" icon={<TeamOutlined />} title=" 员工客户分析">
            <Menu.Item key="1"><Link to='/app/ly3/'>客户总量分析</Link></Menu.Item>
            <Menu.Item key="2"><Link to='/app/ly3/kehu3'>客户跟进次数分析</Link></Menu.Item>
            <Menu.Item key="4"><Link to='/app/ly3/kehu4'>客户跟进方式分析</Link></Menu.Item>
            <Menu.Item key="5"><Link to='/app/ly3/kehu5'>客户转化率分析</Link></Menu.Item>
            <Menu.Item key="6"><Link to='/app/ly3/kehu6'>公海客户分析</Link></Menu.Item>
            <Menu.Item key="7"><Link to='/app/ly3/kehu7'>成交周期分析</Link></Menu.Item>
          </SubMenu>
          <SubMenu key="sub2" icon={<FilterOutlined />} title="销售漏斗分析 ">
            <Menu.Item key="8"><Link to='/app/ly3/loudou2'>销售漏斗</Link></Menu.Item>
            <Menu.Item key="9"><Link to='/app/ly3/loudou3'>新增商机分析</Link></Menu.Item>
            <Menu.Item key="10"><Link to='/app/ly3/loudou4'>商机妆化率分析</Link></Menu.Item>
          </SubMenu>
          <SubMenu key="sub3" icon={<LineChartOutlined />} title="员工业绩分析">
            <Menu.Item key="11"><Link to='/app/ly3/yeji2'>合同数量分析</Link></Menu.Item>
            <Menu.Item key="12"><Link to='/app/ly3/yeji3'>合同金额分析</Link></Menu.Item>
            <Menu.Item key="13"><Link to='/app/ly3/yeji4'>回款金额分析</Link></Menu.Item>
            <Menu.Item key="14"><Link to='/app/ly3/yeji5'>合同汇总表</Link></Menu.Item>
          </SubMenu>
          <SubMenu key="sub4" icon={<UserOutlined />} title="客户画像分析">
            <Menu.Item key="15"><Link to='/app/ly3/hua2'>城市分布分析</Link></Menu.Item>
            <Menu.Item key="16"><Link to='/app/ly3/hua3'>客户行业分析</Link></Menu.Item>
            <Menu.Item key="17"><Link to='/app/ly3/hua4'>客户级别分析</Link></Menu.Item>
            <Menu.Item key="18"><Link to='/app/ly3/hua5'>客户来源分析</Link></Menu.Item>
          </SubMenu>
          <SubMenu key="sub5" icon={<AppstoreOutlined /> }title="产品分析">
            <Menu.Item key="19"><Link to='/app/ly3/product2'>产品销售情况分析</Link></Menu.Item>
            <Menu.Item key="20"><Link to='/app/ly3/product3'>产品分类销售分析</Link></Menu.Item>
          </SubMenu>
          <SubMenu key="sub6" icon={<BarChartOutlined /> }title="排行榜">
            <Menu.Item key="21"><Link to='/app/ly3/pai2'>合同金额排行</Link></Menu.Item>
            <Menu.Item key="22"><Link to='/app/ly3/pai3'>回款金额排行</Link></Menu.Item>
            <Menu.Item key="23"><Link to='/app/ly3/pai4'>签约合同排行</Link></Menu.Item>
            <Menu.Item key="24"><Link to='/app/ly3/pai5'>产品销量排行</Link></Menu.Item>
            <Menu.Item key="25"><Link to='/app/ly3/pai6'>新增客户数排行</Link></Menu.Item>
            <Menu.Item key="26"><Link to='/app/ly3/pai7'>新增联系人排行</Link></Menu.Item>
            <Menu.Item key="27"><Link to='/app/ly3/pai8'>跟进次数排行</Link></Menu.Item>
            <Menu.Item key="28"><Link to='/app/ly3/pai9'>跟进客户数排行</Link></Menu.Item>
            <Menu.Item key="29"><Link to='/app/ly3/pai10'>出差次数排行</Link></Menu.Item>
          </SubMenu>
          <SubMenu key="sub7" icon={ <FundOutlined />}title="办公分析">
            <Menu.Item key="30"><Link to='/app/ly3/ban1'>日志分析</Link></Menu.Item>
            <Menu.Item key="31"><Link to='/app/ly3/ban2'>审批分析</Link></Menu.Item>
          </SubMenu>
            <Menu.Item key="32" icon={ <AimOutlined />}><Link to='/app/ly3/mubiao'>业绩目标完成情况</Link></Menu.Item>
        </Menu>       
      </Sider>
      <Layout className="site-layout">
        <Header className="site-layout-background" style={{ padding: 0 }} />
        <Content style={{ margin: '0 16px' }}>
          <HashRouter>
            <Switch>
             <Route path="/app/ly3/" component={kehu2} exact></Route>
             <Route path="/app/ly3/kehu3" component={kehu3}></Route>
             <Route path='/app/ly3/kehu4' component={kehu4}></Route>
             <Route path='/app/ly3/kehu5' component={kehu5}></Route>
             <Route path='/app/ly3/kehu6' component={kehu6}></Route>
             <Route path='/app/ly3/kehu7' component={kehu7}></Route>
             <Route path='/app/ly3/loudou2' component={loudou2}></Route>
            <Route path='/app/ly3/loudou3' component={loudou3}></Route>
            <Route path='/app/ly3/loudou4' component={loudou4}></Route> 
            <Route path='/app/ly3/yeji2' component={yeji2}></Route>
            <Route path='/app/ly3/yeji3' component={yeji3}></Route>
           <Route path='/app/ly3/yeji4' component={yeji4}></Route>
            <Route path='/app/ly3/yeji5' component={yeji5}></Route>
            <Route path='/app/ly3/hua2' component={hua2}></Route> 
            <Route path='/app/ly3/hua3' component={hua3}></Route> 
            <Route path='/app/ly3/hua4' component={hua4}></Route>
            <Route path='/app/ly3/hua5' component={hua5}></Route> 
            <Route path='/app/ly3/product2' component={product2}></Route>
            <Route path='/app/ly3/product3' component={product3}></Route>
            <Route path='/app/ly3/pai2' component={pai2}></Route>
             <Route path='/app/ly3/pai3' component={pai3}></Route>
            <Route path='/app/ly3/pai4' component={pai4}></Route>
            <Route path='/app/ly3/pai5' component={pai5}></Route>
           <Route path='/app/ly3/pai6' component={pai6}></Route>
            <Route path='/app/ly3/pai7' component={pai7}></Route>
             <Route path='/app/ly3/pai8' component={pai8}></Route>
            <Route path='/app/ly3/pai9' component={pai9}></Route>
            <Route path='/app/ly3/pai10' component={pai10}></Route> 
            <Route path='/app/ly3/ban1' component={ban1}></Route>
            <Route path='/app/ly3/ban2' component={ban2}></Route>
            <Route path='/app/ly3/mubiao'component={mubiao}></Route>
          </Switch>
          </HashRouter>
        </Content>
          <Footer></Footer>
       </Layout>       
    </Layout>
    
    );
  }
}
