import '../css/ly2_10.css'
import { useState } from 'react';
import { Input, Space,Modal, Button,Select } from 'antd';
// import { AudioOutlined } from '@ant-design/icons';
import axios from 'axios';
// import Item from 'antd/lib/list/Item';


 function Ly2_10() {


    function onChange(value) {
        console.log(`selected ${value}`);
      }
      
      function onBlur() {
        console.log('blur');
      }

      function onFocus() {
        console.log('focus');
      }

      function onSearch(val) {
        console.log('search:', val);
      } 

    const { Option } = Select;
    function handleChange(value) {
        console.log(`selected ${value}`);
      }
    // function change(value) {
    //     console.log(value); // { value: "lucy", key: "lucy", label: "Lucy (101)" }
    // }
    const { Search } = Input;

    // const suffix = (
    //     <AudioOutlined
    //         style={{
    //             fontSize: 16,
    //             color: '#1890ff',
    //         }}
    //     />
    // );
    const [isModalVisible, setIsModalVisible] = useState(false);

    function showModal() {
        setIsModalVisible(true);
    };
    function handleOk() {
        setIsModalVisible(false);
    };
    function handleCancel() {
        setIsModalVisible(false);
    };
    const [pullo,setpullo]=useState([])
    axios.post('http://crm.cimns.com/index.php/admin/scene/index',{
        types: 'crm_contract'
    }).then((data)=>{
        if(data.data.data === 101){
            console.log(data.data.error)
        }else{
            if(pullo.length <=0){
                setpullo(data.data.data.list)
            }
        }
        console.log(pullo)
    },(err)=>{
        console.log(err)
    })
        const [spwwoo, setIsModalVisiblel] = useState(false);
      
        function showModalsaa () {
          setIsModalVisiblel(true);
        };
        function handleOkl () {
          setIsModalVisiblel(false);
        };
        function handleCancell () {
          setIsModalVisiblel(false);
        };

    const [spllt,setspllt]=useState([])
    axios.post('http://crm.cimns.com/index.php/admin/field/getField',{
        types: 'crm_product',
        module: 'crm',
        action: 'index',
        controller: 'product',
    }).then((data)=>{
        if(data.data.data === 101){
            console.log(data.data.error)
        }else{
            if(spllt.length <=0){
                setspllt(data.data.data)
            }
        }
        console.log(spllt)
    },(err)=>{
        console.log(err)
    })
    const [libi,setlibi]=useState([])
    axios.post('http://crm.cimns.com/index.php/admin/index/fields',{
        types: 'crm_contract',
        module: 'crm',
        action: 'index',
        controller: 'contract'
    }).then((data)=>{
        if(data.data.data === 101){
            console.log(data.data.error)
        }else{
            if(libi.length <=0){
                setlibi(data.data.data)
            }
        }
        console.log(data.data.data)
    },(err)=>{
        console.log(err)
    })
    return (
        <div className="ly2_10_box">
            <div className="ly2_10_top">
                <div>
                    <h3 className="ly2_10_font">产品管理</h3>
                    <Space direction="vertical" className="ly2_10_width">
                        <Search placeholder="请输入合同名称" />
                    </Space>
                </div>
                <div className="ly2_10_button">
                    <Button type="primary" onClick={showModal}>
                        新建产品
                    </Button>
                    <Modal title="新建产品"  maskStyle={{background:'#ddd'}} cancelText="取消" okText="保存" visible={isModalVisible} onCancel={handleCancel} onOk={handleOk}>
                        <div className="ly2_10_conttm" >
                            <h4>基本信息</h4>
                            <div className="ly2_10_contt_mm">
                                <h5 className="ly2_10_fontaa">*产品名称</h5>
                                <input type="text" className="ly2_10_content_input"></input>
                            </div>
                            <div className="ly2_10_contt_mm">
                                <h5 className="ly2_10_fontaa">*产品类型</h5>
                                <input type="text" className="ly2_10_content_input"></input>
                            </div>
                            <div className="ly2_10_contt_tt">
                                <h5 className="ly2_10_fontaa">*产品编码</h5>
                                <input type="text" className="ly2_10_content_input" placeholder="+添加"></input>
                            </div>
                            <div className="ly2_10_contt_tt">
                                <h5 className="ly2_10_fontaa">*是否上架</h5>
                                <input type="text" className="ly2_10_content_input" placeholder="+添加"></input>
                            </div>
                            <div className="ly2_10_contt_zh">
                                <h5 className="ly2_10_fontaa">*单位</h5>
                                <input type="text" className="ly2_10_content_input" placeholder="+添加"></input>
                            </div>
                            <div className="ly2_10_contt_zh">
                                <h5 className="ly2_10_fontaa">*标准价格</h5>
                                <input type="text" className="ly2_10_content_input" placeholder="+添加"></input>
                            </div>  
                            <div className="ly2_10_contt_zh">
                                <h5 className="ly2_10_fontaa">*产品描述</h5>
                                <input type="text" className="ly2_10_content_input"></input>
                            </div>  
                        </div>
                    </Modal>
                </div>
                <div>
                    <Select defaultValue="更多" style={{ width: 70 }} className="ly2_10_but" onChange={handleChange}>
                        <Option value="Yiminghe">导出</Option>
                    </Select>
                </div>
            </div>
            <div className="ly2_10_content">
              <div className="ly2_10_content_box">
                <div className="ly2_10_lefttop">场景:</div>
                    <Select labelInValue defaultValue={{ value: '全部合同' }} className="ly2_10_content_sai" style={{ width: 120 }} onChange={handleChange} bordered={false}>
                            {pullo.map((item,index)=>{
                                return(
                                    <Option key={index} value={item.name}>
                                        {item.name}
                                    </Option>
                                )
                            })}
                    </Select>
                    <div className="ly2_10_content_topss">
                        <div className="iconfont">&#xe6be;
                        <div className="ly2_10_content_left">
                            <Button  className="ly2_10_saixuan" onClick={showModalsaa}>
                                 高级筛选
                            </Button>
                            <Modal   title="高级筛选" okText="确定" cancelText="取消" visible={spwwoo} onOk={handleOkl} onCancel={handleCancell}>
                                <div>
                                    <h4>筛选条件</h4>
                                    <div className="ly2_10_topbox">
                                        <Select style={{ width: 200 }} placeholder="请选择要筛选的字段名" optionFilterProp="children" onChange={onChange} onFocus={onFocus} onBlur={onBlur} onSearch={onSearch}    filterOption={(input, option) =>    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0    }>
                                        {libi.map((item,index)=>{
                                            return(
                                                <div key={index} className="ly2_10_content_top">
                                                    {item.name}
                                                </div>
                                            )
                                        })}
                                        </Select>
                                        <Select defaultValue="等于" className="ly2_10_lefttopp" style={{ width: 120 }} onChange={handleChange}>
                                            <Option value="等于">等于</Option>
                                            <Option value="不等于">不等于</Option>
                                            <Option value="包含">包含</Option>
                                            <Option value="不包含">不包含</Option>
                                            <Option value="开始于">开始于</Option>
                                            <Option value="结束于">结束于</Option>
                                            <Option value="为空">为空</Option>
                                            <Option value="不为空">不为空</Option>
                                            <Option value="小于">小于</Option>
                                            </Select>
                                            <input type="text" className="ly2_10_inputtun" placeholder="请输入筛选条件"></input>
                                            <div className="ly2_10_topsaixu">+添加筛选条件</div>
                                            <div className="ly2_10_cnontentrr">
                                               <input type="checkbox" /> <span>保存为场景</span> 
                                            </div>
                                    </div>
                                </div>
                            </Modal>
                        </div>
                        </div>
                    </div> 
              </div>

              <div className="ly2_10_contentbottom">
                    <div className="ly2_10_contentbottom_box">
                        {spllt.map((item,index)=>{
                            return(
                                <div key={index} className="ly2_10_content_top">
                                    {item.name}
                                </div>
                            )
                        })}
                    </div>
              </div>
            </div>
        </div>

    )
}
export default Ly2_10