import React, { useState } from 'react'; //组件2
import axios from 'axios'
import { Form, Checkbox } from 'antd';
import 'antd/dist/antd.css'
import { Row, Col } from 'antd';
import {
    PlusOutlined
  } from '@ant-design/icons';
import { Modal, Button,Upload, } from 'antd';
import { DatePicker, Space } from 'antd';
import { UploadOutlined} from '@ant-design/icons';
const style = {margin:'0 10px'};
function Mypro(){
    const props = {
        action: '//jsonplaceholder.typicode.com/posts/',
        listType: 'picture',
        previewFile(file) {
          console.log('Your upload file:', file);
          // Your process logic. Here we just mock to the same file
          return fetch('https://next.json-generator.com/api/json/get/4ytyBoLK8', {
            method: 'POST',
            body: file,
          })
            .then(res => res.json())
            .then(({ thumbnail }) => thumbnail);
        },
      };
    const [isModalVisible, setIsModalVisible] = useState(false);

    function showModal() {
        setIsModalVisible(true);
    };
    function handleOk() {
        setIsModalVisible(false);
    };
    function handleCancel() {
        setIsModalVisible(false);
    };
    const { RangePicker } = DatePicker;
    let [toarr,settoarr]=useState([])
    let [yaarr,setyaarr]=useState([])
    let [xiaarr,setxiaarr]=useState([])
    let [yiarr,setyiarr]=useState([])
    axios.post('http://crm.cimns.com/index.php/work/task/myTask',{}).then((data)=>{
        // console.log(data)
        if(toarr.length>=1){
            settoarr(data.data.data[0].list)
            setyaarr(data.data.data[1].list)
            setxiaarr(data.data.data[2].list)
            setyiarr(data.data.data[3].list)
        }
    },(error)=>{
        console.log(error)
    })
    return(
        <div>
            <div className='top'>
                <h3>我的任务</h3>
            </div>
            <div className='content_child'>
            <Row gutter={16}>
                <Col className="gutter-row" span={5} style={style}>
                        <div className='box'>
                            <h6><b>收件箱</b>  0/0</h6>
                            <div className='boxdiv'></div>
                            <div style={{padding:'10px 0 30px'}}>
                                {toarr.map((item,idx)=>{
                                return(
                                    <div key={idx} style={{width:'100%',height:'60px',marginBottom:'10px',border:'1px solid #e9e9e9',borderRadius:'3px',borderLeft:'2px solid #3e8ee9',boxShadow:'3px 3px 5px #ddd'}}>
                                         <Form.Item style={{textIndent:10}}>
                                            <Checkbox checked={item.checked} style={{color:'#666',lineHeight:'40px'}}>{item.name}</Checkbox>
                                            <p className='iconfont' style={{color:'#ff0000',fontSize:'12px',textIndent:30}}>&#xe66d; 04-19 截止</p>
                                        </Form.Item>
                                    </div>
                                )
                            })}
                            </div>
                            
                            <p onClick={showModal}><PlusOutlined style={{color:'#3e8ee9'}}/> 新建任务</p>
                        </div>
                </Col>
                <Col className="gutter-row" span={5} style={style}>
                        <div className='box'>
                            <h6><b>今天要做</b>  0/0</h6>
                            <div className='boxdiv'></div>
                            <div style={{padding:'10px 0 30px'}}>
                            {yaarr.map((item,idx)=>{
                                return(
                                    <div key={idx} style={{width:'100%',height:'60px',marginBottom:'10px',border:'1px solid #e9e9e9',borderRadius:'3px',borderLeft:'2px solid #3e8ee9',boxShadow:'3px 3px 5px #ddd'}}>
                                         <Form.Item style={{textIndent:10}}>
                                            <Checkbox checked={item.checked} style={{color:'#666',lineHeight:'40px'}}>{item.name}</Checkbox>
                                            <p className='iconfont' style={{color:'#ff0000',fontSize:'12px',textIndent:30}}>&#xe66d; 04-19 截止</p>
                                        </Form.Item>
                                    </div>
                                )
                            })}
                            </div>
                            <p onClick={showModal}><PlusOutlined style={{color:'#3e8ee9'}}/> 新建任务</p>
                         </div>
                </Col>
                <Col className="gutter-row" span={5} style={style}>
                        <div className='box'>
                            <h6><b>下一步要做</b>  0/0</h6>
                            <div className='boxdiv'></div>
                            <div style={{padding:'10px 0 30px'}}>
                            {xiaarr.map((item,idx)=>{
                                return(
                                    <div key={idx} style={{width:'100%',height:'60px',marginBottom:'10px',border:'1px solid #e9e9e9',borderRadius:'3px',borderLeft:'2px solid #3e8ee9',boxShadow:'3px 3px 5px #ddd'}}>
                                         <Form.Item style={{textIndent:10}}>
                                            <Checkbox checked={item.checked} style={{color:'#666',lineHeight:'40px'}}>{item.name}</Checkbox>
                                            <p className='iconfont' style={{color:'#ff0000',fontSize:'12px',textIndent:30}}>&#xe66d; 04-19 截止</p>
                                        </Form.Item>
                                    </div>
                                )
                            })}
                            </div>
                            <p onClick={showModal}><PlusOutlined style={{color:'#3e8ee9'}}/> 新建任务</p>
                        </div>
                </Col>
                <Col className="gutter-row" span={5} style={style}>
                        <div className='box'>
                            <h6><b>以后要做</b>  0/0</h6>
                            <div className='boxdiv'></div>
                            <div style={{padding:'10px 0 30px'}}>
                            {yiarr.map((item,idx)=>{
                                return(
                                    <div key={idx} style={{width:'100%',height:'60px',marginBottom:'10px',border:'1px solid #e9e9e9',borderRadius:'3px',borderLeft:'2px solid #3e8ee9',boxShadow:'3px 3px 5px #ddd'}}>
                                         <Form.Item style={{textIndent:10}}>
                                            <Checkbox checked={item.checked} style={{color:'#666',lineHeight:'40px'}}>{item.name}</Checkbox>
                                            <p className='iconfont' style={{color:'#ff0000',fontSize:'12px',textIndent:30}}>&#xe66d; 04-19 截止</p>
                                        </Form.Item>
                                    </div>
                                )
                            })}
                            </div>
                            <p onClick={showModal}><PlusOutlined style={{color:'#3e8ee9'}}/> 新建任务</p>
                        </div>
                </Col>
            </Row>
            </div>
            <Modal title="新建任务" okText="保存" cancelText="取消" width={700} maskStyle={{ background: '#eee',height:'100%'}} visible={isModalVisible} onOk={handleOk} onCancel={handleCancel} bodyStyle={{padding:40,fontSize: '12px'}} style={{marginTop:-70}}>
                <div style={{display:'flex',flexWrap:'wrap',justifyContent:'space-between'}}>
                    <div className='proinput'><p><b style={{fontWeight:'none',color:'red'}}>*</b>任务名称</p><input type="text" className="ass acc"></input></div>
                    <div className='proinput'><p>负责人</p><input type="text" className="ass acc"></input></div>
                    <div className='proinput'><p>开始时间-结束时间</p>
                        <Space direction="vertical" size={12}>
                            <RangePicker />
                        </Space>
                    </div>
                </div>
                <div style={{marginBottom:35}}>
                    <p>优先级</p>
                    <ul className='prolist'>
                        <li style={{border:'1px solid rgb(243, 125, 125)',color:'rgb(243, 125, 125)'}}>高</li><li style={{border:'1px solid rgb(230, 128, 70)',color:'rgb(230, 128, 70)'}}>中</li><li style={{border:'1px solid rgb(70, 158, 230)',color:'rgb(70, 158, 230)'}}>低</li><li style={{backgroundColor:'#ccc',color:'#fff'}}>无</li>
                    </ul>
                </div>
                <div>
                    <p>任务描述</p>
                    <textarea className="no2_2_axx" style={{border:'1px solid #ccc'}}></textarea>
                </div>
                <div className="no1_2_guanlian">
                    <Upload {...props}>
                        <Button icon={<UploadOutlined />}>关联业务</Button>
                    </Upload>        
                </div>
            </Modal>
        </div>
    )
}
export default Mypro
