
import { Table } from 'antd';
import axios from 'axios'
import React, { useEffect,useState } from 'react'

function Tab14() {
  const [arr,setArr]=useState([]);
  /**
* 获取表格数据(同Promise)
* @param {回调} callback 
*/
  function TableInit() {
    axios.post('http://crm.cimns.com/index.php/bi/contract/summary', {
      structure_id: 1,
      user_id: '',
      type: 'year'
    }).then(({ data }) => {
     let newarr = data.data.items.map((item, index) => {
        return {
          key: index,
          type: item.type,
          age:0,
          address: index,
        }
      })
      console.log(newarr);
      setArr(newarr)
    }, (err) => {
      console.log(err)
    })
  }

  //callback(回调写法)
  
  //解决hook重新渲染导致死循环
  useEffect(() => {
    //解决异步(并不可取，建议不要使用)
    TableInit();
  }, []);
  const columns = [
    {
      title: '日期',
      width: 90,
      dataIndex: 'type',
      key: 'type',
    },

    {
      title: '签约合同数',
      width: 95,
      dataInde:'item.age',
      key: 'item.age',
    },
    {
      title: '签约合同金额',
      dataIndex: 'address',
      key: 'address',
      width: 90,
    },
    {
      title: '回款合同金额',
      dataIndex: 'address',
      key: 'address',
      width: 90,
    },


  ];
  return (
    <div>
      <Table columns={columns} dataSource={arr} pagination={{ pageSize: 40 }} scroll={{ y: 400 }} />

    </div>
  );
}

export default Tab14;