import { Tabs } from 'antd';
import React, { useState } from 'react';
import axios from 'axios'
import { Collapse } from 'antd';


function Ly1_7(){
    const { TabPane } = Tabs;

function callback(key) {
  console.log(key);
}
const { Panel } = Collapse;


const [arr,setarr] = useState([])
const [arrx,setarrx] = useState([])

axios.post('http://crm.cimns.com/index.php/oa/addresslist/index',{
    type: 1,
  }).then((data)=> {
    if(data.data.code===200){
        if(arr.length <= 0){  
            let arr1 = data.data.data
            let arr2 = []
            let arr3 = []
            for (var i in arr1) {
                arr2.push(arr1[i]); 
                arr3.push(i)
            }
            setarr(data.data.data)
            setarrx(arr3)
        }
    }else{
      console.log('off4失败');
    }
  })
  console.log(arr);
  console.log(arrx);
    return(
        <div className='ly17'>
            <Tabs defaultActiveKey="1" onChange={callback}>
                <TabPane tab="员工" key="1">
                    <div className='ly17-top'>
                        <span className='iconfont'></span><input type='text' placeholder='搜索成员' />
                    </div>
                    <div className='ly17-end'>
                    <ul className="off7-com">
                    {arrx.map((item, index) => {
                      return (
                        <li className="off7-item" key={index}>
                          <p className="of7-title">{item}</p>
                          {arr[item].map((itm, inde) => {
                            return (
                              <div className="itsl" key={inde}>
                                <p className="name">{itm.realname}</p>
                                <div className="xit">
                                  <p>
                                    <i className="iconfont">&#xe705;</i>{" "}
                                    {itm.structure_name}
                                  </p>
                                  <p
                                    style={{
                                      display: itm.post ? "block" : "none",
                                    }}
                                  >
                                    <i className="iconfont">&#xe617;</i>
                                    {itm.post}
                                  </p>
                                  <p>
                                    <i className="iconfont">&#xe621;</i>
                                    {itm.username}
                                  </p>
                                </div>
                              </div>
                            );
                          })}
                        </li>
                      );
                    })}
                  </ul>
                    </div>
                </TabPane>
                <TabPane tab="部门" key="2">
                    <div className='ly17-top'>
                        <span className='iconfont'></span><input type='text' placeholder='搜索成员' />
                    </div>
                    <div>
                    <Collapse onChange={callback}>
                        <Panel header="办公室" key="1">
                        <div className='ly17-end'>
                    <ul className="off7-com">
                    {arrx.map((item, index) => {
                      return (
                        <li className="off7-item" key={index}>
                          <p className="of7-title">{item}</p>
                          {arr[item].map((itm, inde) => {
                            return (
                              <div className="itsl" key={inde}>
                                <p className="name">{itm.realname}</p>
                                <div className="xit">
                                  <p>
                                    <i className="iconfont">&#xe705;</i>{" "}
                                    {itm.structure_name}
                                    <img src=""  alt="" />
                                  </p>
                                  <p
                                    style={{
                                      display: itm.post ? "block" : "none",
                                    }}
                                  >
                                    <i className="iconfont">&#xe617;</i>
                                    {itm.post}
                                  </p>
                                  <p>
                                    <i className="iconfont">&#xe621;</i>
                                    {itm.username}
                                  </p>
                                </div>
                              </div>
                            );
                          })}
                        </li>
                      );
                    })}
                  </ul>
                    </div>
                        </Panel>
                        <Panel header="打杂部" key="2">
                        
                        </Panel>
                    </Collapse>
                    </div>
                </TabPane>
            </Tabs>
        </div>
    )
}

export default Ly1_7