
import { Table } from 'antd';
import axios from 'axios'
import React , {useEffect,} from 'react'

let arr=[];

 function Tab19(){
    // const [arr4,setarr4] = useState([]);
    // let data = [];
    
    //callback(回调写法)
     TableInit((res)=>{
        arr=res;
     });
     //解决hook重新渲染导致死循环
     useEffect(()=>{
       //解决异步(并不可取，建议不要使用)
      setTimeout(()=>{
        arr=arr.map((item,index)=>{
        return{
          key: index,
          name: item.category_id_info,

          age: 32,
          address: index,
        }
      });
        // setarr4(arr);
      },1000);
     },[]);
      console.log(arr)
     
     const columns = [
        {
          title: '产品分类',
          width: 90,
          dataIndex: 'category_id_info',
          key: 'category_id_info',
        },
       
        {
          title: '产品名称',
          width: 95,
          dataIndex: 'product_name',
          key: 'product_name',
        },
        {
          title: '合同编号',
          dataIndex: 'contract_name',
          key: 'contract_name',
          width: 90,
        },
        {
          title: '负责人',
          dataIndex: 'realname',
          key: 'realname',
          width: 90,
        },
        {
          title: '客户名称',
          dataIndex: '打看法',
          key: '打看法',
          width: 90,
        },
        {
          title: '销售单价',
          dataIndex: 'price',
          key: 'price',
          width: 90,
        },
        {
          title: '数量',
          dataIndex: 'num',
          key: 'num',
          width: 80,
        },
        {
          title: '订单产品小计',
          dataIndex: 'num',
          key: 'num',
          width: 100,
        },
        
      ];
      
  
  
return(
<div>
    <Table  columns={columns} dataSource={arr} pagination={{ pageSize: 40 }} scroll={{ y: 400 }} />
</div>
);
}
/**
 * 获取表格数据(同Promise)
 * @param {回调} callback 
 */
function TableInit(callback){
  axios.post('http://crm.cimns.com/index.php/bi/product/statistics',{
    structure_id: 1,
      type: 'year'
  }).then((data)=>{
    callback([...data.data.data])
  },(err)=>{
      console.log(err)
    
  })
}
export default Tab19;