
/////
import { Table } from 'antd';
import axios from 'axios'
import React, { useEffect,useState } from 'react'
const columns = [
  {
    title: '商机名称',
    dataIndex: 'name',
    align:'center',
  },
  {
    title: '客户名称',
    dataIndex: 'aga',
    align:'center',
  },
  {
    title: '商机状态组',
    dataIndex: 'agb',
    align:'center',
  },{
    title: '商机阶段',
    dataIndex: 'agc',
    align:'center',
  },
  {
    title: '商机金额',
    dataIndex: 'agd',
    align:'center',
  },
  {
    title: '预计成交日期',
    dataIndex: 'age',
    align:'center',
  },{
    title: '负责人',
    dataIndex: 'agf',
    align:'center',
  },
  {
    title: '创建时间',
    dataIndex: 'agg',
    align:'center',
  },
];
function Tab9() {
  const [arr,setarr] = useState([])
  useEffect(() => {
    axios.post('http://crm.cimns.com/index.php/bi/business/trendList',{
      structure_id: 1,
      user_id: '',
      type: 'year'
    }).then((data)=> {
      setarr(data.data.data)
      console.log(data.data.data);
    })
  },[])

  const data = [];
  for (let i = 0; i < arr.length; i++) {
    console.log(i)
    data.push({
      key: i,
      name:arr[i].business_name,
      aga:arr[i].customer_name,
      agb:arr[i].type_id_info,
      agc:arr[i].status_id_info,
      agd:arr[i].money,
      age:arr[i].deal_date, 
      agf:arr[i].create_user_name,
      agg:arr[i].create_time,
    });
  }
    return(
      <div >
          <Table columns={columns} dataSource={data} pagination={false} bordered='true' style={{height:400}}/>
      </div>
    )
}

export default Tab9;

