import List8 from '../components/list8'
import '../css/ly2_2_1.css'
import { useState } from 'react';
import axios from 'axios';

function Ly2_2_7(){
    const [spllt,setspllt]=useState([])
    axios.post('http://crm.cimns.com/index.php/admin/field/getField',{
        types: 'crm_contract',
        module: 'crm',
        controller: 'contract',
        action: 'index',
    }).then((data)=>{
        if(data.data.data === 101){
            console.log(data.data.error)
        }else{
            if(spllt.length <=0){
                setspllt(data.data.data)
            }
        }
        console.log(spllt)
    },(err)=>{
        console.log(err)
    })
    return(
            <div className="Todo____list">
                <p className="contact">即将到期的合同</p>
                <div className="contactor">
                    <div className="customer"><List8></List8></div>
                    <div className="custom">
                        <p><i className="iconfont">&#xe6be;</i>高级筛选</p>
                    </div>
                </div>
                <div className="ly2_2_contentbottom">
                    <div className="ly2_2_contentbottom_box">
                        {spllt.map((item,index)=>{
                            return(
                                <div key={index} className="ly2_2_top">
                                    {item.name}
                                </div>
                            )
                        })}
                    </div>
                </div>
            </div>
    )
}
export default Ly2_2_7