import './css/login.css'
import {useState} from 'react'
import axios from 'axios'

function Login(props){
    const [json,setjson]  = useState({username:'',password:''})

    function getval(event){
        let dom = event.target
        let type = dom.getAttribute('login')
        let val = dom.value.split(' ').join('')
        let newjson = {...json}
        newjson[type] = val
        // console.log(newjson[type]);
        setjson(newjson)
    }
    function btn(){
        ctn()
        if(json.username === '' || json.password === ''){
            // alert('请输入内容')        
        }
        else{
            axios.post('http://crm.cimns.com/index.php/admin/base/login',json).then((data)=>{
                if(data.data.code === 200){
                    localStorage.login = JSON.stringify(data.data.data)
                    // console.log(localStorage.login);
                    props.history.push('/App')
                }
                else if(data.data.code === 400){
                    alert('登入失败')
                }
            })
        }
    }
    function ctn(){
        if(json.username === ''){
            setnone({display:'block'})
        }
        if(json.password === ''){
            setnone2({display:'block'})
        } 
    }

    const [none,setnone] = useState ({display:'none'})
    const [none2,setnone2] = useState ({display:'none'})
    return(
        <div className='login clearfix'>
            <div className='login-fl'>
                <img src="http://crm.cimns.com/static/img/logo.3e34073.png"  alt="" />
            </div>
            <div className='login-fr'>
                <div className='login-fr-m'>
                    <h3>悟空CRM</h3>
                <div className='m10'>
                    <div className='input'>
                        <input type='text' login='username' value={json.username} onChange={getval} placeholder='请输入用户名' />
                        <p style={none}>请输入账号</p>
                    </div>
                    <div className='input'>
                        <input type='text' login='password' value={json.password} onChange={getval} placeholder='请输入密码' />
                        <p style={none2}>密码不能小于5位</p>
                    </div>
                </div>
                <div className='button'><button onClick={btn}>登入</button></div>
                </div>
            </div>
        </div>
    )
}
export default Login