import { Layout, Menu} from 'antd';
import React,{Component}from 'react'
import {Switch,Route,Link} from 'react-router-dom'

import Ly2_1 from './ly2_1'
import Ly2_2 from './ly2_2'
import Ly2_3 from './ly2_3'
import Ly2_4 from './ly2_4'
import Ly2_5 from './ly2_5'
import Ly2_6 from './ly2_6'
import Ly2_7 from './ly2_7'
import Ly2_8 from './ly2_8'
import Ly2_9 from './ly2_9'
import Ly2_10 from './ly2_10'


import 'antd/dist/antd.css'
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
  } from '@ant-design/icons';
  
  const { Sider, Content } = Layout;
  let type = true
  
  export default class Ly1 extends Component {
    state = {
      collapsed: false,
    }
    toggle = () => {
      this.setState({
        collapsed: !this.state.collapsed,
      });
    };
  
    render() {
      return (
        <Layout style={{minHeight:'100%'}} className='ppoe'>
          <Sider trigger={null} collapsible collapsed={this.state.collapsed}>
            <div className="logo2">
              <div className='ks'>
                <span style={{display:type === true?'block':'none'}}>快速创建</span><i>&gt;</i>
              </div>
            </div>
            <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']}>
              <Menu.Item key="1" icon={<i className='iconfont'>&#xe66d;</i>}>
                <Link to="/app/ly2">仪表盘</Link>
              </Menu.Item>
              <Menu.Item key="2" icon={<i className='iconfont'>&#xe62b;</i>}>
                <Link to="/app/ly2/ly2_2">待办事项</Link>
              </Menu.Item>
              <Menu.Item key="3" icon={<i className='iconfont'>&#xe681;</i>}>
                <Link to="/app/ly2/ly2_3">线索</Link>
              </Menu.Item>
              <Menu.Item key="4" icon={<i className='iconfont'>&#xe68e;</i>}>
                <Link to="/app/ly2/ly2_4">客户</Link>
              </Menu.Item>
              <Menu.Item key="5" icon={<i className='iconfont'>&#xe627;</i>}>
                <Link to="/app/ly2/ly2_5">联系人</Link>
              </Menu.Item>
              <Menu.Item key="6" icon={<i className='iconfont'>&#xe611;</i>}>
                <Link to="/app/ly2/ly2_6">公海</Link>
              </Menu.Item>
              <Menu.Item key="7" icon={<i className='iconfont'>&#xe649;</i>}>
                <Link to="/app/ly2/ly2_7">商机</Link>
              </Menu.Item>
              <Menu.Item key="8" icon={<i className='iconfont'>&#xe63c;</i>}>
                <Link to="/app/ly2/ly2_8">合同</Link>
              </Menu.Item>
              <Menu.Item key="9" icon={<i className='iconfont'>&#xe604;</i>}>
                <Link to="/app/ly2/ly2_9">回款</Link>
              </Menu.Item>
              <Menu.Item key="10" icon={<i className='iconfont'>&#xe62f;</i>}>
                <Link to="/app/ly2/ly2_10">产品</Link>
              </Menu.Item>
            </Menu>
            <div className='btn1' onClick={()=>{type = !type}}>
              {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
                className: 'trigger',
                onClick: this.toggle,
              })}
            </div>
          </Sider>
          <Layout className="site-layout">
            <Content className="site-layout-background" style={{minHeight: 280,}} >
              <Switch>
              <Route path="/app/ly2" component={Ly2_1} exact></Route>
                <Route path="/app/ly2/ly2_2" component={Ly2_2}></Route>
                <Route path="/app/ly2/ly2_3" component={Ly2_3}></Route>	
                <Route path="/app/ly2/ly2_4" component={Ly2_4}></Route>
                <Route path="/app/ly2/ly2_5" component={Ly2_5}></Route>
                <Route path="/app/ly2/ly2_6" component={Ly2_6}></Route>
                <Route path="/app/ly2/ly2_7" component={Ly2_7}></Route>
                <Route path="/app/ly2/ly2_8" component={Ly2_8}></Route>
                <Route path="/app/ly2/ly2_9" component={Ly2_9}></Route>
                <Route path="/app/ly2/ly2_10" component={Ly2_10}></Route>
              </Switch>
            </Content>
          </Layout>
        </Layout>
      );
    }
  }