
import '../css/ly2_6.css'
import { useState } from 'react';
import { Input, Space,Modal, Button,Select } from 'antd';
// import { AudioOutlined } from '@ant-design/icons';
import axios from 'axios';
// import Item from 'antd/lib/list/Item';



 function Ly2_6() {

    function onChange(value) {
        console.log(`selected ${value}`);
      }
      
      function onBlur() {
        console.log('blur');
      }

      function onFocus() {
        console.log('focus');
      }

      function onSearch(val) {
        console.log('search:', val);
      } 

    const { Option } = Select;
    function handleChange(value) {
        console.log(`selected ${value}`);
      }
    // function change(value) {
    //     console.log(value); // { value: "lucy", key: "lucy", label: "Lucy (101)" }
    // }
    const { Search } = Input;

    // const suffix = (
    //     <AudioOutlined
    //         style={{
    //             fontSize: 16,
    //             color: '#1890ff',
    //         }}
    //     />
    // );

    // function showModal() {
    //     setIsModalVisible(true);
    // };
    const [spwwoo, setIsModalVisiblel] = useState(false);
    function showModalsaa () {
        setIsModalVisiblel(true);
    };
    function handleOkl () {
        setIsModalVisiblel(false);
    };
    function handleCancell () {
        setIsModalVisiblel(false);
    };

    const [spllt,setspllt]=useState([])
    axios.post('http://crm.cimns.com/index.php/admin/field/getField',{
        types: 'crm_customer',
        module: 'crm',
        action: 'pool',
        controller: 'customer',
    }).then((data)=>{
        if(data.data.data === 101){
            console.log(data.data.error)
        }else{
            if(spllt.length <=0){
                setspllt(data.data.data)
            }
        }
        console.log(spllt)
    },(err)=>{
        console.log(err)
    })
    const [libi,setlibi]=useState([])
    axios.post('http://crm.cimns.com/index.php/admin/index/fields',{
        types: 'crm_contract',
        module: 'crm',
        action: 'index',
        controller: 'contract',
    }).then((data)=>{
         if(data.data.data === 101){
            console.log(data.data.error)
        }else{
            if(libi.length <=0){
                setlibi(data.data.data)
            }
        }
        console.log(data.data.data)
    },(err)=>{
        console.log(err)
    })
    return (
        <div className="ly2_6_box">
            <div className="ly2_6_top">
                <div>
                    <h3 className="ly2_6_font">公海管理</h3>
                    <Space direction="vertical" className="ly2_6_width">
                        <Search placeholder="请输入合同名称" />
                    </Space>
                </div>
                <div>
                    <Select defaultValue="更多" style={{ width: 70 }} className="ly2_6_but" onChange={handleChange}>
                        <Option value="Yiminghe">导出</Option>
                    </Select>
                </div>
            </div>
            <div className="ly2_6_content">
              <div className="ly2_6_content_box">
                    <div className="ly2_6_content_topss">
                        <div className="iconfont">&#xe6be;
                        <div className="ly2_6_content_left">
                            <Button  className="ly2_6_saixuan" onClick={showModalsaa}>
                                 高级筛选
                            </Button>
                            <Modal   title="高级筛选" okText="确定" cancelText="取消" visible={spwwoo} onOk={handleOkl} onCancel={handleCancell}>
                                <div>
                                    <h4>筛选条件</h4>
                                    <div className="ly2_6_topbox">
                                        <Select style={{ width: 200 }} placeholder="请选择要筛选的字段名" optionFilterProp="children" onChange={onChange} onFocus={onFocus} onBlur={onBlur} onSearch={onSearch}    filterOption={(input, option) =>    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0    }>
                                        {libi.map((item,index)=>{
                                            return(
                                                <div key={index} className="ly2_6_content_top">
                                                    {item.name}
                                                </div>
                                            )
                                        })}
                                        </Select>
                                        <Select defaultValue="等于" className="ly2_6_lefttopp" style={{ width: 120 }} onChange={handleChange}>
                                            <Option value="等于">等于</Option>
                                            <Option value="不等于">不等于</Option>
                                            <Option value="包含">包含</Option>
                                            <Option value="不包含">不包含</Option>
                                            <Option value="开始于">开始于</Option>
                                            <Option value="结束于">结束于</Option>
                                            <Option value="为空">为空</Option>
                                            <Option value="不为空">不为空</Option>
                                            <Option value="小于">小于</Option>
                                            </Select>
                                            <input type="text" className="ly2_6_inputtun" placeholder="请输入筛选条件"></input>
                                            <div className="ly2_6_topsaixu">+添加筛选条件</div>
                                            <div className="ly2_6_cnontentrr">
                                               <input type="checkbox" /> <span>保存为场景</span> 
                                            </div>
                                    </div>
                                </div>
                            </Modal>
                        </div>
                        </div>
                    </div> 
              </div>

              <div className="ly2_6_contentbottom">
                    <div className="ly2_6_contentbottom_box">
                        {spllt.map((index)=>{
                            return(
                                <div key={index} className="ly2_6_content_top">
                                    {index.name}
                                </div>
                            )
                        })}
                    </div>
              </div>
            </div>
        </div>
    )
}
export default Ly2_6